import { PageHero } from "@/components/ui/page-hero"
import { LeadForm } from "@/components/forms/lead-form"
import { Sidebar } from "@/components/content/sidebar"
import { WhyChooseInsura } from "@/components/content/why-choose-insura"
import { FAQ } from "@/components/content/faq"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Check, Shield } from "lucide-react"

const coverageTypes = [
  { title: "Comprehensive Coverage", description: "Full protection against accidents, theft, fire, and vandalism" },
  { title: "Third-Party Liability", description: "Covers damage or injury caused to others" },
  { title: "Personal Accident Cover", description: "Protection for the rider" },
]

const addOns = ["Roadside Assistance", "Agency Repair", "Passenger Cover", "Helmet Cover", "Personal Belongings Cover"]

const faqs = [
  {
    question: "What documents are required for bike insurance?",
    answer:
      "You need a valid UAE driving license, Emirates ID, vehicle registration card (Mulkiya), and previous insurance policy if renewing.",
  },
  {
    question: "Is bike insurance mandatory in UAE?",
    answer: "Yes, third-party liability insurance is mandatory for all motorcycles registered in the UAE.",
  },
  {
    question: "How do I file a claim?",
    answer:
      "Contact our 24/7 claims helpline, provide your policy details and accident information, and our team will guide you through the process.",
  },
]

export default function BikeInsurancePage() {
const [formData, setFormData] = useState({
    company: "",
    person: "",
    mobile: "",
    email: "",
  })

    const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    console.log("Form submitted:", formData)
  }
  return (
    <main>
      {/* HERO BANNER */}
      <section className="relative overflow-hidden bg-primary min-h-[40vh]">
        <div className="absolute inset-0">
          <img
            src="/bike1.jpg"
            alt="Bike Insurance"
            className="w-full h-full object-center"
          />
          <div className="absolute inset-0 bg-gradient-to-r from-primary via-secondary/90 to-transparent" />
        </div>

        <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-32">
          <div className="max-w-2xl text-white">
            <div className="flex items-center gap-2 text-white/80 mb-4 text-sm">
              <span>insura.ae</span>
              <span className="w-1.5 h-1.5 bg-white rounded-full" />
              <span>Motor Insurance</span>
            </div>

            <h1 className="text-4xl md:text-5xl font-bold mb-4">
              Car Insurance
            </h1>

            <p className="text-lg text-white/90">
              Drive with confidence knowing Insura has you covered with
              comprehensive car insurance solutions.
            </p>
          </div>
        </div>
      </section>

      <section className="py-16 bg-background">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">

          <div className="mb-8">
            <h2 className="text-3xl md:text-4xl font-bold text-primary leading-snug">
              Bike Insurance in UAE
            </h2>
            <h2 className="text-3xl md:text-4xl text-gray-500 leading-snug">
              With <span className="font-bold text-gray-500">BIKE INSURANCE</span> you ride worry free in UAE!!!
            </h2>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
            <div className="lg:col-span-2 space-y-12">
              {/* Lead Form Section */}
               <h3 className="text-3xl text-primary mb-2">
                Fill the form to get information about Bike Insurance
              </h3>
              <p>
                Give us a few details and we’ll offer the best quotation.
              </p>

<form onSubmit={handleSubmit} className="space-y-8">
                {/* Name */}
                <div>
                  <Label className="font-medium">
                    Name <span className="text-red-500">*</span>
                  </Label>
                  <Input
                    placeholder="Enter full name..."
                    className="
                      mt-2
                      h-14
                      rounded-xl
                      bg-blue-50
                      border-none
                      placeholder:text-muted-foreground
                      focus-visible:ring-2
                      focus-visible:ring-primary
                    "
                  />
                </div>

                {/* Email */}
                <div>
                  <Label className="font-medium">
                    Email Address <span className="text-red-500">*</span>
                  </Label>
                  <Input
                    placeholder="Enter email id..."
                    type="email"
                    className="
                      mt-2
                      h-14
                      rounded-xl
                      bg-blue-50
                      border-none
                      placeholder:text-muted-foreground
                      focus-visible:ring-2
                      focus-visible:ring-primary
                    "
                  />
                </div>

                {/* Mobile Number */}
                <div>
                  <Label className="font-medium">
                    Mobile Number <span className="text-red-500">*</span>
                  </Label>
                  <Input
                    placeholder="+971 Enter mobile number..."
                    className="
                      mt-2
                      h-14
                      rounded-xl
                      bg-blue-50
                      border-none
                      placeholder:text-muted-foreground
                      focus-visible:ring-2
                      focus-visible:ring-primary
                    "
                  />
                </div>

                {/* Car Brand */}
                <div>
                  <Label className="font-medium">
                    What is your car's brand?
                  </Label>
                  <Input
                    placeholder="BMW"
                    className="
                      mt-2
                      h-14
                      rounded-xl
                      bg-blue-50
                      border-none
                      focus-visible:ring-2
                      focus-visible:ring-primary
                    "
                  />
                </div>

                {/* Car Model */}
                <div>
                  <Label className="font-medium">
                    Select BMW Model
                  </Label>
                  <Input
                    placeholder="3 Series"
                    className="
                      mt-2
                      h-14
                      rounded-xl
                      bg-blue-50
                      border-none
                      focus-visible:ring-2
                      focus-visible:ring-primary
                    "
                  />
                </div>

                {/* Registration Year */}
                <div>
                  <Label className="font-medium">
                    In which year your Car got registered
                  </Label>
                  <Input
                    placeholder="2025"
                    className="
                      mt-2
                      h-14
                      rounded-xl
                      bg-blue-50
                      border-none
                      focus-visible:ring-2
                      focus-visible:ring-primary
                    "
                  />
                </div>

                {/* Submit */}
                <div className="pt-2">
                  <Button
                    type="submit"
                    className="
                      bg-primary
                      hover:bg-primary/90
                      px-2
                      py-4
                    "
                  >
                    Send Application
                  </Button>
                </div>
              </form>

              {/* Why Bike Insurance */}
              <div>
                <h2 className="text-2xl font-bold text-foreground mb-4">Why Bike Insurance is Mandatory in UAE</h2>
                <p className="text-muted-foreground mb-4">
                  In the UAE, all registered motorcycles must have at least third-party liability insurance. This
                  protects you financially if you cause damage to someone else's property or injure another person while
                  riding.
                </p>
                <p className="text-muted-foreground">
                  Beyond legal compliance, bike insurance provides peace of mind and financial protection against theft,
                  accidents, and natural disasters.
                </p>
              </div>

              {/* Coverage Types */}
              <div>
                <h2 className="text-2xl font-bold text-foreground mb-6">Coverage Types</h2>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                  {coverageTypes.map((type, index) => (
                    <div key={index} className="bg-card rounded-lg p-6 border shadow-sm">
                      <div className="w-12 h-12 bg-primary/10 rounded-lg flex items-center justify-center mb-4">
                        <Shield className="w-6 h-6 text-primary" />
                      </div>
                      <h3 className="font-semibold text-card-foreground mb-2">{type.title}</h3>
                      <p className="text-muted-foreground text-sm">{type.description}</p>
                    </div>
                  ))}
                </div>
              </div>

              {/* Add-ons */}
              <div>
                <h2 className="text-2xl font-bold text-foreground mb-6">Available Add-ons</h2>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {addOns.map((addon, index) => (
                    <div key={index} className="flex items-center gap-3">
                      <div className="w-6 h-6 bg-primary/10 rounded-full flex items-center justify-center">
                        <Check className="w-4 h-4 text-primary" />
                      </div>
                      <span className="text-foreground">{addon}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>

            <div>
              <Sidebar />
            </div>
          </div>
        </div>
      </section>

      <WhyChooseInsura />
      <FAQ items={faqs} />
    </main>
  )
}
